import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import {
    Header, HeaderName, HeaderNavigation, HeaderMenuItem, HeaderGlobalBar, HeaderGlobalAction,
    Tooltip, SideNav, SideNavItems, HeaderSideNavItems, HeaderMenu, HeaderPanel, SkipToContent,
    ListItem, UnorderedList
} from "carbon-components-react";
import { TableOfContents } from "@carbon/icons-react";
import { EventAPI } from "../../commons/EventAPI";
import { Button } from "@carbon/react";
import SrsIcon from "./Icon";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-pageheader header_name="Srste" header_title="Srste [Platform]" 
                menu_items=[{name: "home", "link": "/home"}, {name: "builder", link: "/builder"}]>
            </srs-pageheader>
*/
const SrsPageHeader = function (props, ref) {
    const [isSideNavExpanded, setIsSideNavExpanded] = useState(false);
    const [isPanelOpen, setIsPanelOpen] = useState(false);
    const [activePanel, setActivePanel] = useState(null);
    const [sideNavParent, setSideNavParent] = useState(null);
    const [sideNavGrantParent, setSideNavGrantParent] = useState(null);
    const [sideNavMenuItems, setSideNavMenuItems] = useState(null);

    let openPanel = false;
    let activePanelName = "";
    let eventBinding = false;

    function handleUXSettings() {
        openPanel = activePanelName == "settings" ? !openPanel : true;
        activePanelName = openPanel ? "settings" : "";
        setIsPanelOpen(openPanel);
        setActivePanel(activePanelName);
    }

    function handleNavigation() {
        openPanel = activePanelName == "navigation" ? !openPanel : true;
        activePanelName = openPanel ? "navigation" : "";
        setIsPanelOpen(openPanel);
        setActivePanel(activePanelName);
    }

    function handleAppSwitcher() {
        openPanel = activePanelName == "appswitcher" ? !openPanel : true;
        activePanelName = openPanel ? "appswitcher" : "";
        setIsPanelOpen(openPanel);
        setActivePanel(activePanelName);
    }

    function handleSearch() {
        openPanel = activePanelName == "search" ? !openPanel : true;
        activePanelName = openPanel ? "search" : "";
        setIsPanelOpen(openPanel);
        setActivePanel(activePanelName);
    }

    function interceptEventTarget(event) {
        if (event.composed) {
            Object.defineProperty(event, 'target', {
                value: event.composedPath()[0],
                writable: false,
                enumerable: true,
                configurable: true
            });
        }
    }

    function handleEvent(event) {
        interceptEventTarget(event);
    }

    function handleSideNavExpanded(value, dispatchEvent) {
        setIsSideNavExpanded(value);
        dispatchEvent && dispatchEvent(EventAPI.HEADER_SIDENAV_EXPANDED, { expanded: value });

    }

    useEffect(() => {

        if (!eventBinding) {
            ref.current.addEventListener(EventAPI.HEADER_UX_SETTINGS_CLICKED, handleUXSettings);
            ref.current.addEventListener(EventAPI.HEADER_APPSWITCHER_CLICKED, handleAppSwitcher);
            ref.current.addEventListener(EventAPI.HEADER_NAVIGATION_CLICKED, handleNavigation);
            ref.current.addEventListener(EventAPI.HEADER_SEARCH_CLICKED, handleSearch);

            // Add the event listener to the window object
            ref.current.addEventListener('mouseup', handleEvent);
            ref.current.addEventListener('mousedown', handleEvent);
            ref.current.addEventListener('mouseover', handleEvent);
            ref.current.addEventListener('mouseleave', handleEvent);
            ref.current.addEventListener('mouseenter', handleEvent);


            ref.current.addEventListener('touchstart', handleEvent);
            ref.current.addEventListener('touchmove', handleEvent);
            ref.current.addEventListener('touchend', handleEvent);
            props.dispatchEvent(EventAPI.HEADER_ADDED);
            eventBinding = true;
        }

        // Remove the event listener when the component unmounts
        return () => {
            ref.current.removeEventListener(EventAPI.HEADER_UX_SETTINGS_CLICKED, handleUXSettings);
            ref.current.removeEventListener(EventAPI.HEADER_APPSWITCHER_CLICKED, handleAppSwitcher);
            ref.current.removeEventListener(EventAPI.HEADER_NAVIGATION_CLICKED, handleNavigation);
            ref.current.removeEventListener(EventAPI.HEADER_SEARCH_CLICKED, handleSearch);

            ref.current.removeEventListener('mouseup', handleEvent);
            ref.current.removeEventListener('mousedown', handleEvent);
            ref.current.removeEventListener('mouseover', handleEvent);
            ref.current.removeEventListener('mouseleave', handleEvent);
            ref.current.removeEventListener('mouseenter', handleEvent);

            ref.current.removeEventListener('touchstart', handleEvent);
            ref.current.removeEventListener('touchmove', handleEvent);
            ref.current.removeEventListener('touchend', handleEvent);

        };
    }, [props.name]);

    const handleMenuItemClick = (path, dispatchEvent) => {
        return (evt) => {
            evt.preventDefault();
            window.history.pushState(null, '', path);
            props.dispatchEvent("NAVIGATE_TO_PATH", { path });
            setSideNavMenuItems(null);
            handleSideNavExpanded(false, dispatchEvent);
        }
    }

    const findChildItems = (items, parentItemName) => {
        if (parentItemName) {
            items.forEach((item) => {
                if (item.name == parentItemName) {
                    return item.children;
                } else if (item.children) {
                    return findChildItems(item.children, parentItemName);
                }
            });
        } else {
            return items;
        }
        return items[0].children;
    }

    const CustomHeaderMenuItem = function (itemProps) {
        const [isOpen, setIsOpen] = useState(false);

        const handleMenuClick = (evt) => {
            setIsOpen(true);
        }

        const handlePopoverClose = () => {
            setIsOpen(false);
        }

        //TODO - Render the submenu items
        return null;
    }

    let sideNavItems = findChildItems(props.sidenav_items, sideNavParent);
    if (sideNavParent) {
        sideNavItems = [{
            name: sideNavGrantParent,
            iconName: "hand-point-left",
            navigateback: true,
            label: "Back",
            iconType: "fontawesome",
            color: "var(--cds-text-primary)",
            size: 20
        }].concat(sideNavItems);
    }
    let fixedModuleEl = (<div className="fixedMenu">
        {
            sideNavItems ? sideNavItems.map((sidenavItem, indx) => {
                return (<div className="fixedMenuItemFreeText iconView">
                    <SrsIcon type={sidenavItem.iconType || "carbon"} name={sidenavItem.iconName}
                        color={sidenavItem.color} size={sidenavItem.size || 20}
                        description={sidenavItem.label}
                        onClick={(evt) => {
                            if (sidenavItem.children || sidenavItem.navigateback) {
                                setSideNavGrantParent(sideNavParent);
                                setSideNavParent(sidenavItem.name);
                                setSideNavMenuItems(null);
                                handleSideNavExpanded(false, props.dispatchEvent);
                            } else if (sidenavItem.menuItems) {
                                if (sidenavItem.menuItems && sidenavItem.menuItems.length > 0) {
                                    setSideNavMenuItems(sidenavItem.menuItems);
                                    handleSideNavExpanded(true, props.dispatchEvent);
                                } else {
                                    setSideNavMenuItems(null);
                                    handleSideNavExpanded(false, props.dispatchEvent);
                                }
                            }
                        }} />
                </div>)
            }) : null
        }
    </div>);

    let sideNavMainEl = sideNavMenuItems && sideNavMenuItems.length > 0 ? (<SideNav
        aria-label="Side navigation"
        expanded={isSideNavExpanded}
        isPersistent={false}>
        <SideNavItems>
            {
                sideNavMenuItems.map((menuItem) => {
                    if (menuItem.children) {
                        return (<HeaderMenu aria-label={menuItem.label} menuLinkName={menuItem.label}>
                            {
                                menuItem.children.map((submenuItem) => {
                                    return (<HeaderMenuItem onClick={handleMenuItemClick(submenuItem.link, props.dispatchEvent)}>{submenuItem.label}</HeaderMenuItem>);
                                })
                            }
                        </HeaderMenu>)
                    }
                    return (<HeaderMenuItem onClick={handleMenuItemClick(menuItem.link, props.dispatchEvent)}>{menuItem.label}</HeaderMenuItem>);
                })
            }
        </SideNavItems>
    </SideNav>) : null;

    let inlineStyles = {
        "--cds-background-hover": "var(--cds-layer-accent)"
    }

    let headerPanel = null;

    if (activePanel) {
        switch (activePanel) {
            case "settings":
                headerPanel = (
                    <div style={{ display: "flex", flexDirection: "column", padding: 40 }}>
                        <h4>Choose a theme</h4>
                        <div style={{ padding: 20 }}>
                            <UnorderedList>
                                <ListItem style={{ cursor: "pointer" }} onClick={() => {
                                    props.dispatchEvent(EventAPI.PAGE_THEME_CHANGE, { theme: "white" });
                                }}>Light Theme</ListItem>
                                <ListItem style={{ cursor: "pointer" }} onClick={() => {
                                    props.dispatchEvent(EventAPI.PAGE_THEME_CHANGE, { theme: "g10" });
                                }}>Grey-10 Theme</ListItem>
                                <ListItem style={{ cursor: "pointer" }} onClick={() => {
                                    props.dispatchEvent(EventAPI.PAGE_THEME_CHANGE, { theme: "g90" });
                                }}>Grey-90 Theme</ListItem>
                                <ListItem style={{ cursor: "pointer" }} onClick={() => {
                                    props.dispatchEvent(EventAPI.PAGE_THEME_CHANGE, { theme: "g100" });
                                }}>Dark Theme</ListItem>
                            </UnorderedList>
                        </div>
                    </div>
                );
                break;
            case "appswitcher":
                break;
            case "navigation":
                break;
            case "search":
                break;
        }
    }

    return (<Header aria-label={props.header_title} ref={ref} style={inlineStyles}>
        <SkipToContent />
        {fixedModuleEl}
        <TableOfContents
            size={24}
            aria-label="Open menu"
            onClick={() => handleSideNavExpanded(!isSideNavExpanded, props.dispatchEvent)}
        />
        <HeaderName href="#" prefix="">{props.hader_name || props.header_title}</HeaderName>
        <HeaderNavigation aria-label={props.header_navigation || props.header_title}>
            {
                props.menu_items && props.menu_items.map((menuItem, indx) => {
                    if (menuItem.items) {
                        return (<CustomHeaderMenuItem clickHandler={handleMenuItemClick} items={menuItem.items} name={menuItem.name}></CustomHeaderMenuItem>);
                    } else {
                        return (<HeaderMenuItem onClick={handleMenuItemClick(menuItem.link, props.dispatchEvent)} tabIndex={indx}>{menuItem.name}</HeaderMenuItem>);
                    }
                })
            }
        </HeaderNavigation>
        {sideNavMainEl}
        <HeaderGlobalBar>
            <HeaderGlobalAction aria-label="Search" isActive={activePanel == "search"}>
                <slot name="search"></slot>
            </HeaderGlobalAction>
            <HeaderGlobalAction aria-label="Notifications" isActive={activePanel == "navigation"}>
                <slot name="notifications"></slot>
            </HeaderGlobalAction>
            <HeaderGlobalAction aria-label="App Switcher" isActive={activePanel == "appswitcher"}>
                <slot name="appswitcher"></slot>
            </HeaderGlobalAction>
            <HeaderGlobalAction aria-label="Settings" isActive={activePanel == "settings"}>
                <slot name="settings"></slot>
            </HeaderGlobalAction>
        </HeaderGlobalBar>
        <HeaderPanel aria-label="Header Panel" expanded={isPanelOpen}>
            {headerPanel}
        </HeaderPanel>
    </Header>);
}

const styles = `
    .fixedMenu {
        top: 3rem;
        position: absolute;
        background: var(--cds-layer);
        width: 60px;
        height: calc(100vh - 3rem);
        cursor: pointer;
    }

    .fixedMenuItem {
        background-color: var(--cds-layer);
        margin-left: 5px;
        margin-top: 5px;
        margin-bottom: 5px;
        border-radius: 3px;
        cursor: pointer;
    }

    .fixedMenuItemFreeText {
        margin-left: 10px;
        margin-top: 10px;
        margin-bottom: 5px;
        border-radius: 3px;
        width: 40px;
        height: 25px;
        padding-left: 2px;
        padding-right: 2px;
        cursor: pointer;
        font-size: medium;
        text-align: center;
        padding-top: 0px;
        font-weight: 600;
    }

    .fixedMenuItemFreeText:hover {
        border: 2px solid #efe7ed;
    }

    .iconView {
        background-color: var(--cds-layer); !important;
    }

    .cds--side-nav__navigation {
        margin-left: 60px !important;
    }

    .cds--side-nav__navigation {
        border-right: 1px solid var(--cds-layer-accent);
    }
`
const props = [{
    name: "header_name",
    defaultValue: "Srste",
    type: "string"
}, {
    name: "header_title",
    defaultValue: "Srste",
    type: "string"
}, {
    name: "header_navigation",
    defaultValue: "Srste",
    type: "string"
}, {
    name: "menu_items",
    defaultValue: [],
    type: "object"
}, {
    name: "sidenav_items",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsPageHeader), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };